import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const uiModule = {
    state: {
        count: 15,
        user:{
            name:"南山彭于晏"
        },
        system:{
           name:"网点管理"
        }
    },
    mutations: {
        incrementDouble(state) {
            state.count++;
        },
        setUser(state, user) {
            for (var k in user) {
                state.user[k] = user[k];
            }
        },
        setSystem(state, user) {
            for (var k in user) {
                state.system[k] = user[k];
            }
        },
    },
    getters: {
        doubleCount(state) {
            return state.count * 2
        }
    }
}

const store = new Vuex.Store({
    modules: {
        ui: uiModule
    },
    state: {
        count: 0,
        routeTree: null,
        lhttp:{
            // platform_token:"twxpjjs7jfmpskhotw2djkxr5explkhn",
            urlHead: 'http://192.168.10.247:8099/crm-api/v2',
            urlHeads: 'http://192.168.10.247/crm-api',
            AddToBDPrivate:"/shop/public/addToBDPrivate",//添加业务员到私海
            addToPrivate:"/shop/public/addToPrivate",//添加到自己的私海
            create:"/shop/public/create",//新建网点
            search:"/shop/public/search",//搜索公海网点列表
            applyShopDetail:"/shop/private/findApplyingShopDetail",//查询申请中的网点详情
            fllowShopDetail:"/shop/private/findFollowingShopDetail",//查询跟进中的网点详情
            giveup:"/shop/private/giveUp",//放弃私海网点
            enterShop:"/shop/private/listEnteredShops",//查询已经入住的私海网点
            notEnterShop:"/shop/private/notEnterShops",//查询未入住的私海网点
            shopCount:"/shop/private/shopCount",//查询个人私海网点数量
            areaLists: '/cash/area/list',//新的省市级联器
        }
    },
    mutations: {
        initRoute(state, params) {
            console.log(params);
            state.routeTree = params.routeTree
        },
        increment(state, params) {
            state.count++;
        }
    }
})

export default store
